import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let defaultCity = '上海'
try{
  if(localStorage.city){
    defaultCity= localStorage.city
  }
}
catch(e){

}
export default new Vuex.Store({
  state: {
    city: defaultCity
  },
  mutations: {
    changeCity(state,city){
       state.city = city  
       try{
        localStorage.city=city

       }
       catch(e){

       }
    }
  },
  // actions: {


  // }
  //类似组件的计算属性
  getters:{
    doubleCity (state) {
      return state.city + ' ' + state.city
    }
  }
  
})
